import React, { useState, useEffect } from "react";
import axios from "axios";
import Nodes from "./webSocket";

import {
  ComposableMap,
  Geographies,
  Geography,
  Marker,
  ZoomableGroup,
} from "react-simple-maps";

const geoUrl = process.env.REACT_APP_NODES_MAP_URL;

const NodeChart = (props) => {
  const [node, setNode] = useState([]);

  let ip = props?.ipcount;
  // console.log('ip-------', ip)

  useEffect(() => {
    // let ip = props.ipcount
    // console.log('pr---',ip)
    ip.map((item) => {
      axios
        .get(process.env.REACT_APP_NODE_LOCATIONS + item)
        .then((res) => {
          var nodes = node;
          nodes.push({ coords: [res.data.lon, res.data.lat] });
          // console.log('locations....', nodes)
          setNode(nodes);
        })
        .catch((err) => {
          console.log(err);
        });
    });
  }, [ip]);

  // console.log('propsip',props?.ipcount)

  return (
    <div>
      <ComposableMap
        className={props.dark ? "composableNodeDarkmap" : "composableNodemap"}
      >
        {/* <ZoomableGroup zoom={1}> */}
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map((geo) => (
              <Geography key={geo.rsmKey} geography={geo}></Geography>
            ))
          }
        </Geographies>

        {node.map((items) => (
          <Marker coordinates={items.coords}>
            <g
              fill="#10c892"
              stroke="#10c892"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              transform="translate(-12, -24)"
            >
              <circle cx="12" cy="" r="9" />
            </g>
          </Marker>
        ))}
        {/* </ZoomableGroup> */}
      </ComposableMap>
    </div>
  );
};

export default NodeChart;
